html,
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  text-align: center;
  background: black;
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

.app {
  width: 1600px;
  margin: 0 auto;
}

.column {
  float: left;
  width: 10%;
  padding: 10px;
  text-align: center;
  cursor: pointer;
}

.column:hover {
  background: #222;
}

.proximity-indicator {
  width: 10px;
  height: 10px;
  margin: 0 auto -10px;
  background: white;
  border-radius: 10px;
}

.double-meter .meter {
  float: left;
  clear: none;
}

.meter {
  width: 50%;
  height: 50px;
  margin: 20px auto;
  clear: both;

  border-top-right-radius: 40px;
  border-top-left-radius: 40px;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
}

.led {
  text-align: center;
  display: inline-block;
  width: 14px;
  height: 14px;
  margin: 26px 1px 0;

  border-radius: 16px;
  background: black;
  -webkit-filter: blur(2px);
          filter: blur(2px);
}



